/* ABOUT US  */
.homeaboutus,
.iotsection,
.article {
  width: 100%;
  min-height: 100vh;
}

.sectionContainer,
.iotcontainer,
.articlecontainer {
  width: 85%;
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
}
.sectionContainer {
  justify-content: space-between;
  align-items: center;
}
.leftabout {
  position: relative;
  width: 45%;
  /* margin: auto; */
}

.rightabout {
  width: 45%;
  /* margin: auto; */
}

.leftaboutpic {
  width: 83%;
  border-radius: 15px;
  position: relative;
  display: inline-block;
  max-width: 100%;
  height: auto;
  overflow: hidden;
}
.leftaboutpic::before {
  position: absolute;
  top: 0;
  left: -75%;
  display: block;
  content: "";
  width: 50%;
  height: 100%;
  background: -o-linear-gradient(
    left,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.3) 100%
  );
  background: -webkit-gradient(
    linear,
    left top,
    right top,
    from(rgba(255, 255, 255, 0)),
    to(rgba(255, 255, 255, 0.3))
  );
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.3) 100%
  );
  -webkit-transform: skewX(-25deg);
  -ms-transform: skewX(-25deg);
  transform: skewX(-25deg);
}

.leftaboutpic2 {
  background: url("https://nessco.kafkaindia.com/Assets/images/resource/nessco-team-pic1.webp") top center/contain;
  /* border: 1px solid black; */
  position: absolute;
  width: 200px;
  height: 200px;
  bottom: -90px;
  right: 0px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* position: relative; */
  /* zoom: 115%; */
  /* transform: scale(0.8); */
  /* transform:scale3d(); */
}


.playbtn {
  color: var(--redcolor);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
  width: 70px;
  font-size: 2rem;
  border-radius: 1000%;
  background-color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);

  border: none;
  /* z-index: -1; */
}

