.playbtn:after {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  border-radius: 50%;
  border: 3px solid white;
  animation: hamburger_puls 2s ease infinite;
}
.playbtn:before {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  border-radius: 50%;
  border: 3px solid white;
  animation: hamburger_puls2 2s 1s ease infinite;
}

.aboutusbtn {
  background-color: red;
  background-image: linear-gradient(45deg, #483d73, #e11e25 100%);
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  width: fit-content;
  padding: 1rem 2rem;
  margin-bottom: 2rem;
  border-radius: 100px;
  font-weight: 300;
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.1);
}

.aboutheading {
  font-size: 2.4rem;
  color: var(--bluecolor);
  margin-bottom: 2rem;
  font-family: "Inter", sans-serif;
}

.small {
  font-size: 1.5rem;
  font-weight: 600;
}

.aboutheading > span {
  color: var(--redcolor);
}

.aboutpara {
  font-weight: 400;
  color: #595959;
  
  font-size: 1.4rem;
  margin-bottom: 2rem;
  word-spacing: 4px;
  font-family: "Open Sans", sans-serif;
}

.aboutbtnsection {
  display: flex;
}
.aboutpara > a {
  color: var(--bluecolor);
}
.aboutpara > a:hover {
  color: var(--redcolor);
}
.getaquotebtn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  font-weight: 600;
