  border: none;
  transition: all 0.5s ease;
  margin-top: 3rem;
}

.getaquotebtn:hover {
  background-color: var(--redcolor);
}

.signatureimg {
  width: 30%;
  margin-left: 2rem;
}

/* IOT SECTION  */

.leftiot {
  width: 45%;
  margin: auto;
}
.rightiot {
  width: 45%;
  margin: auto;
}
.iotheading {
  color: var(--bluecolor);
  font-size: 3.5rem;
}
.iotheading > span {
  color: var(--redcolor);
}
.iotpara {
  font-size: 1.4rem;
  line-height: 25px;
  color: #595959;

  word-spacing: 1px;
  margin-top: 1rem;
  text-align: justify;
}
.iotlist {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  margin: 2rem 0;
}
.iotlist > li {
  width: 50%;
  margin: 0.8rem 0;
  font-size: 1.4rem;
  position: relative;
  display: flex;
  align-items: center;
  /* justify-content: space-evenly; */
  line-height: 25px;
  flex-wrap: wrap;
  flex-direction: row;
  z-index: -1;
  color: #595959;
  padding-left: 1.5rem;
}
.iotlist li:before {
  position: absolute;
  content: "";
  width: 12px;
  height: 12px;
  left: 0px;
  top: 6px;
  border-radius: 50%;
  background: -webkit-linear-gradient(45deg, #483d73, #e11e25 100%);
  z-index: -1;
}
.iotlist li:after {
  position: absolute;
  left: 2px;
  top: 8px;
  content: "";
  background: #fff;
  width: 8px;
  height: 8px;
  border-radius: 50%;
}
.rightiot > img {
  width: 100%;
}
.aboutusbtn {
  font-weight: 500;
  font-size: 1.3rem;
}
