/* SUBSCRIBE */

.subscribe {
  width: 100%;
  height: 100px;
  background-color: var(--bluecolor);
}
.icon1 {
  width: 50px;
}
.subsribecontainer {
  width: 87%;
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.leftsubscribe,
.rightsubscribe {
  height: 100%;
  width: 45%;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.subsribeheading {
  margin-left: 2rem;
  font-size: 1.8rem;
  color: white;
  font-weight: 500;
}
.rightsubscribe > input {
  padding: 2rem;
  font-size: 1.5rem;
  width: 70%;
  height: 55%;
}
.subscribebtn {
  width: 30%;
  height: 55%;
  margin-left: 1.75rem;
  /* padding: 2rem; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  background-color: #001b47;
  border: none;
  color: white;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: 0.5s;
  z-index: 0;
}
.subscribebtn:hover {
  /* background-color: white; */
  color: var(--redcolor);
  /* background-color: transparent; */
}
.subscribebtn::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 0%;
  background: white;
  z-index: -1;
  color: var(--bluecolor);
  transition: 0.8s;
  top: 0;
  border-radius: 0 0 50% 50%;
}
.subscribebtn:hover::before {
  height: 180%;
}

/* ARTICLE  */
.article {
  background: #f0f0f0;
}
.articlecontainer {
  width: 88.5%;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.articleheadings {
  font-size: 3rem;
  color: var(--bluecolor);
}
.articleheadings > span {
  color: var(--redcolor);
}
.articlecards {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  margin: 4rem 0;
}
.articlecard {
  width: 30%;
  /* border: 2px solid black; */
  position: relative;
  overflow: hidden;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.uppersection {
  display: flex;
  width: 100%;
  height: 270px;
  overflow: hidden;
  position: relative;
}
.uppersectionoverlaylayer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(0deg, #483d73, #e11e25 100%);

  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 1;
}
.uppersection > img {
  width: 100%;
  transition: all 1s ease;
}
.articlecard:hover .uppersection > img {
  position: relative;
  transform: scale(1.1);
  overflow: hidden;
}
.articlecard:hover .uppersection .uppersectionoverlaylayer {
  opacity: 0.7;
}
